// Ëã•ÊûúË¶ÅÊåÇËΩΩÂéüÂûã app.config.globalProperties
import defaultImg from '@/assets/image/loding.svg'

export default {
    install(app) {
        auto()
        //ÂõæÁâáÊáíÂä†ËΩΩÊåá‰ª§
        defineDirective(app)
    }
}

//Ëá™ÂÆö‰πâ Êåá‰ª§
const defineDirective = (app) => {
    app.directive('lazy',{
        mounted(el,binding) {
            const observe = new IntersectionObserver(([{isIntersecting}]) => {
                if(isIntersecting) {
                    observe.unobserve(el)
                    if(binding.value) {
                        el.src = binding.value
                        console.log('ÂõæÁâáÂä†ËΩΩÊàêÂäü')
                    }
                    if(!binding.value) {
                        el.src = defaultImg
                        console.log('ÂõæÁâáÂä†ËΩΩÂ§±Ë¥•')
                    }
                }
            },{threshold:0.01})
            observe.observe(el)
        },
    })
}

//ËÆæÁΩÆË∑üÂ≠ó‰ΩìÂ§ßÂ∞è
function auto() {
    let clientWidth = document.documentElement.clientWidth
    let rootFS = clientWidth * 100 / 375
    document.documentElement.style.fontSize = rootFS + 'px'
    console.log('auto ')
}
window.onresize = auto